* {
  font-weight: 900;
}
.App {
  background-color: rgb(21, 32, 43);
  height: 100vh;
  width: 100vw;
  color: ghostwhite;
  text-align: center;
  background-image: url("../public/ttten.svg");
  background-size: cover;
  font-family: "Lato", sans-serif;
}
.intro {
  font-family: "Hanalei Fill";
  font-weight: 500;
  width: 80%;
  margin-left: 10%;
  background-color: darkcyan;
  height: 5vh;
  border-radius: 0 0 2rem 2rem;
  margin-top: 0;
  font-size: x-large;
  outline: solid black 0.5vh;
  border-top: 0;
  background-color: rgba(128, 0, 128, 0.8);
  box-shadow: 5px 5px 2.5px 0px rgba(0, 0, 0, 0.75);
  color: black;
}

.score {
  width: 80%;
  margin-left: 10%;
  border-bottom: 0;
  outline: solid black 0.5vh;
  background-color: darkcyan;
  border-radius: 2rem 2rem 0 0;
  bottom: 0;
  background-color: rgba(128, 0, 128, 0.8);
  position: fixed;
  box-shadow: 5px 5px 2.5px 0px rgba(0, 0, 0, 0.75);
}
img {
  height: 70%;
}

.level {
  height: 3vh;
  background-color: red;
  color: ghostwhite;
  font-size: x-large;
  width: 80%;
  margin-left: 10%;
  margin-top: 0.5vh;
  border-radius: 2rem 2rem 0 0;
  border-bottom: none;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 5px 5px 2.5px 0px rgba(0, 0, 0, 0.75);
}
.level > :nth-of-type(1) {
  font-family: "Hanalei Fill", cursive;
}
.level > .streaksNeeded {
  margin-right: 5%;
  font-size: small;
  color: black;
  background-color: #00bd56;
}
.level > .level1 {
  width: 50%;
}
.stats {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  text-align: start;
  margin-left: 10%;
  height: max-content;
  box-shadow: 5px 5px 2.5px 0px rgba(0, 0, 0, 0.75);
  font-size: small;
  background-color: black;
}
.stats > :nth-of-type(1) {
  width: 60%;
  padding-left: 10%;
}
.stats > :nth-of-type(2) {
  width: 30%;
  text-align: center;
}
.stats > :nth-of-type(3) {
  width: 60%;
  padding-left: 10%;
}
.stats > :nth-of-type(4) {
  width: 30%;
  text-align: center;
}

.tips {
  height: max-content;
  width: 80%;
  margin-left: 10%;
  background-color: black;
  border-radius: 10px;
  color: ghostwhite;
  box-shadow: 5px 5px 2.5px 0px rgba(0, 0, 0, 0.75);
  background-image: linear-gradient(90deg, purple 5%, blue, #00bd56 95%);
}
.tips > :nth-of-type(1) {
  background-color: black;
}
.howToPlayLi {
  list-style-type: none;
  border-top: solid red;
  border-bottom: solid red;
}
.mainGrid {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin-left: 5%;
  border-left: solid black;
  border-right: solid black;
  height: 58vh;
  box-sizing: border-box;
  background-color: rgba(24, 26, 27, 0.5);
  /* From https://css.glass */
  backdrop-filter: blur(5px);
  box-shadow: 5px 5px 2.5px 0px rgba(0, 0, 0, 0.75);
}
@media only screen and (min-width: 500px) {
  .mainGrid {
    width: 80%;
    margin-left: 10%;
  }
}
@media only screen and (min-width: 1000px) {
  .mainGrid {
    width: 50%;
    margin-left: 25%;
  }
}
@media only screen and (min-width: 1500px) {
  .mainGrid {
    width: 40%;
    margin-left: 30%;
  }
}
.wrapperSquare {
  width: 22%;
  margin: 0 1.5%;
  outline: black solid;
  border-radius: 25px;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  height: 10%;
}
.square {
  width: 100%;
  height: 100%;
  border-radius: 25px;
  opacity: 0.01;
  transition: 3s ease opacity;
}

#divRows {
  display: flex;
  flex-wrap: nowrap;
  padding: 1vh 0;
  background-color: rgb(24, 26, 27);
  width: 80%;
  margin-left: 10%;
  margin-top: 1vh;
}
.even {
  height: 0.5vh;
  width: 3vw;
  background-color: black;
}
.odd {
  height: 0.5vh;
  width: 3vw;
  background-color: darkcyan;
}

.bigDiv {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 1;
  top: 0;
  left: 0;
  position: fixed;
  color: ghostwhite;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.shown {
  opacity: 1 !important;
  transition: 3s ease opacity;
}

.hidden2 {
  display: none;
}

button {
  width: 40%;
  padding: 1vh 0%;
  background-color: gold;
  border-radius: 2rem;
  border: solid black;
  font-size: x-large;
  height: max-content;
  margin-top: 0.5vh;
  box-shadow: 5px 5px 2.5px 0px rgba(0, 0, 0, 0.75);
  font-family: "Hanalei Fill", cursive;
  font-weight: 500;
}

#restartGame {
  background-color: red;
}
button:hover {
  background-color: #00bd56;
  width: 60%;
}
#restartGame:hover {
  color: ghostwhite;
  width: 60%;
}
.resultsIn {
  width: 98%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50%;
  border-radius: 20px;
  border: solid black;
  font-size: x-large;
}
.resultCorrect {
  background-color: rgb(25, 150, 28);
}
.resultWrong {
  background-color: rgb(255, 0, 0);
}
.hovered {
  outline: solid red;
}
.attempts {
  color: ghostwhite;
}
.greenOutline {
  outline: solid red;
}
